<template>
  <div>
    <div class="head-login">
      <main class="content">
        <router-link to="/">
          <img src="../assets/img/logo.png" alt="">
        </router-link>
      </main>
    </div>
    <div class="register">
      <main class="content">
        <form class="loginForm">
          <h2>用户注册</h2>
          <div class="form-group">
            <label for="username">username</label>
            <p>
              <span class="glyphicon glyphicon-user"></span>
              <input type="text" class="form-control" id="username" v-model="username" placeholder="username" autocomplete="off">
            </p>
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <p>
              <span class="glyphicon glyphicon-envelope"></span>
              <input type="email" class="form-control" id="email" v-model="email" placeholder="jane.doe@example.com">
            </p>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <p>
              <span class="glyphicon glyphicon-eye-open"></span>
              <input type="password" class="form-control" id="password" v-model="password" placeholder="Password" autocomplete="off">
            </p>
          </div>
          <div class="form-group">
            <label for="password2">Confirm Password</label>
            <p>
              <span class="glyphicon glyphicon-eye-open"></span>
              <input type="password" class="form-control" id="password2" v-model="password2" placeholder="Confirm Password" autocomplete="off">
            </p>
          </div>
          <button type="button" class="btn btn-danger" @click="signup">登录</button>
          <div class="checkbox">
            <label>
              <input type="checkbox"> 下次自动登陆
            </label>
          </div>
        </form>
        <div class="process">
          <h2 class="title">我已注册账号</h2>
          <router-link to="/login" class="btn btn-info">立即登录</router-link>
          <img src="../assets/img/register-reg-4.png" alt="">
        </div>
      </main>
    </div>
  </div>
</template>
<script>
  export default {
    name: "signup",
    data() {
      return {
        username: "",
        email: "",
        password: "",
        password2:""
      }
    },
    methods: {
      signup() {
        let username = this.username;
        let password = this.password;
        sessionStorage.setItem("username", username);
        sessionStorage.setItem("password", password);
        this.$router.push("/");
      }
    }
  }

</script>
<style>
  .head-login {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .head-login main {
    display: flex;
    justify-content: flex-start;
  }

  .register {
    background-color: #f7f7f7;
  }

  .register main {
    margin-top: 22px;
    padding: 80px 100px;
    background-color: #fff;
    border: 1px solid #EAEAEA;
    display: flex;
    justify-content: space-between;
  }

  .register main .loginForm {
    width: 360px;
    border-right: 1px solid #EAEAEA;
  }

  .register main .loginForm .form-group {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .register main .loginForm h2,
  .register main .loginForm label {
    text-align: left;
  }

  .register main .loginForm p {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .register main .loginForm p span {
    font-size: 20px;
    margin-right: 10px;
  }

  .register main .loginForm .btn {
    width: 100%;
    padding: 10px 0;
  }

  .register main .loginForm .checkbox {
    display: flex;
    justify-content: flex-start;
  }

  .process {
    width: 340px;
  }

  .process .title {
    margin-bottom: 15px;
  }

  .process .btn {
    margin-bottom: 70px;
    padding: 10px 135px;
    font-size: 16px;
  }

</style>



