<template>
  <div class="block">
    <main class="content">
      <h3>{{data.caption}}</h3>
      <ul class="canvas">
        <li class="item" v-for="(item,key) in data.son" v-bind:key="key">
          <a href="">
            <img v-bind:src="item.photo" alt="">
          </a>
          <div class="warp">
            <div>
              <b class="title">
                <a href="">{{item.title}}</a>
              </b>
              <span class="description">
                <a href="">{{item.description1}}</a>-
                <a href="">{{item.description2}}</a>
              </span>
            </div>
          </div>
          <p class="info">
            <span>{{item.prise}}</span>
            <span>{{item.view}}</span>
          </p>
        </li>
      </ul>
    </main>
  </div>
</template>
<script>
  export default {
    name: 'block',
    props: ["block"],
    data() {
      return {
        data: this.block
      }
    }
  }

</script>
<style>
  .block {
    margin-top: 50px;
  }

  .block h3 {
    text-align: left;
  }

  .block .canvas {
    margin-top: 10px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .block .canvas .item {
    width: 294px;
    margin-right: 8px;
    margin-bottom: 11px;
    padding-bottom: 10px;
    background-color: #fff;
  }
  .block .canvas .item:nth-child(4n+4){
    margin-right: 0;
  }

  .block .canvas .item:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, .2)
  }

  .block .canvas .item>a {
    width: 294px;
    height: 294px;
    display: block;
    text-align: center;
    line-height: 290px;
  }

  .block .canvas .item>a img {
    max-width: 294px;
    max-height: 294px;
  }

  .block .canvas .item .warp {
    height: 54px;
    position: relative;
  }

  .block .canvas .item .warp div {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 100;
  }

  .block .canvas .item .warp div .title {
    padding-top: 10px;
    padding-bottom: 5px;
    font-weight: normal;
    line-height: 22px;
    text-align: center;
    display: block;
  }

  .block .canvas .item .warp div .title>a {
    color: #333;
  }

  .block .canvas .item .warp div .description {
    font-size: 12px;
    color: #919191;
    text-align: center;
  }

  .block .canvas .item .warp div .description>a {
    color: #919191;
  }
  .block .canvas .item .warp div .description>a:hover{
    color: #ef353d;
  }

  .block .canvas .item .info {
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
  }

  .block .canvas .item .info span:first-child {
    margin-left: 60px;
    font-size: 12px;
    color: #ef353d;
  }

  .block .canvas .item .info span:last-child {
    margin-right: 60px;
    padding-left: 15px;
    font-size: 12px;
    color: #71cd9c;
    background-image: url("../assets/img/cover-page-attente.png");
    background-repeat: no-repeat;
    background-position: left center;
    cursor: pointer;
  }

</style>



