<template>
  <div>
    <myhead></myhead>
    <mynav></mynav>
    <div class="place">
      <main class="content">
        您现在的位置：
        <router-link to="/">首页</router-link>
        <span class="glyphicon glyphicon-chevron-right"></span>
        <router-link to="category">插件区</router-link>
      </main>
    </div>
    <div class="info">
      <main class="content">
        <table class="table table-hover">
          <thead>
            <th>分类</th>
            <th>类型</th>
            <th>价格</th>
            <th>操作</th>
          </thead>
          <tbody>
            <tr v-for="(item,key) in table" v-bind:key="key">
              <td>{{item.cate}}</td>
              <td>{{item.type}}</td>
              <td>{{item.prise}}</td>
              <button class="btn btn-info">查看详情</button>
            </tr>
          </tbody>
        </table>
      </main>
    </div>
    <div class="music-bg" style="height: 100%;filter: blur(100px);transition:all 0.3s" id="music-bg">
      <div class="music-mask"></div>
    </div>
  </div>
</template>
<script>
  import myhead from '@/components/myhead'
  import mynav from '@/components/mynav'
  import '@/assets/css/animate.css'
  import '@/assets/css/music.css'
  import '@/assets/fonts/fontCss.css'
  import '@/assets/js/jquery.min.js'
  import '@/assets/js/music.js'
  export default {
    name: "category",
    components: {
      myhead,
      mynav
    },
    data() {
      return {
        table: [{
            cate: "收费插件",
            type: "在线客服",
            prise: "0-3000"
          },
          {
            cate: "免费插件",
            type: "常用工具",
            prise: "3000 - 6000"
          },
          {
            cate: "手机端插件",
            type: "手机客户端",
            prise: "6000 - 9000"
          },
          {
            cate: "免费插件",
            type: "常用工具",
            prise: "9000 - 12000"
          },
          {
            cate: "免费插件",
            type: "常用工具",
            prise: "18000 - 21000"
          }
        ]
      }
    },
    mounted() {
      MC.music({
        hasAjax: false,
        left: '50%',
        bottom: '50%',
        musicChanged: function (ret) {
          // alert(ret.url);
          // getMusic_buffer(ret.url);
          // return;
          var data = ret.data;
          var index = ret.index;
          var imageUrl = data[index].img_url;

          var music_bg = document.getElementById('music-bg');
          music_bg.style.background = 'url(' + imageUrl + ')no-repeat';
          music_bg.style.backgroundSize = 'cover';
          music_bg.style.backgroundPosition = 'center 30%';
        },
        getMusicInfo: function (data) {
        },
        musicPlayByWebAudio: function (ret) {
        },
      });
    }
  }

</script>
<style>
  .place {
    padding-left: 20px;
    line-height: 52px;
    color: #333;
    font-size: 12px;
    background-color: #f2f2f2;
    text-align: left;
  }

  a {
    color: #333;
  }

  span {
    color: #333;
    margin: 0 10px;
  }

  div.info {
    margin-top: 30px;
  }

  th,
  td {
    text-align: center;
    padding: 15px 20px!important;
  }

  td:hover {
    background-color: rgba(136, 102, 170, .4);
  }

  .music-div {
    left: 80%!important;
    top: 20%!important;
  }
  .music-info{
    height: auto!important;
  }

</style>



