<template>
  <div class='head-fix' id="top">
    <main class="content">
      <ul class="top1">
        <li>
          <a href="">模板堂首页</a>
        </li>
        <li>
          <a href="">电商大会</a>
        </li>
        <li>
          <a href="">社区</a>
        </li>
        <li>
          <a href="">知识堂</a>
        </li>
      </ul>
      <div class="right">
        <template v-if="login">
          <span v-if="login">{{username}}</span>
          <span @click="logout">logout</span>
        </template>
        <ul class="top2" v-else>
          <li>
            <router-link to="/login">登陆</router-link>
          </li>
          <li>
            <router-link to="/signup">注册</router-link>
          </li>
        </ul>
      </div>
    </main>
    <a href="#top" class="back">
      <i></i>
    </a>
  </div>
</template>
<script>
  export default {
    name: 'myhead',
    data() {
      return {
        login: false,
        username: ""
      }
    },
    methods: {
      logout() {
        sessionStorage.removeItem("username");
        sessionStorage.removeItem("password");
        this.$router.push("/login");
      }
    },
    created() {
      if (sessionStorage.getItem("username")) {
        this.login = true;
        this.username = sessionStorage.getItem("username");
        console.log(this.username)
      }
    }
  }

</script>
<style>
  .head-fix {
    height: 40px;
    background-color: #363636;
    display: flex;
    align-items: center;
  }

  .head-fix .content {
    display: flex;
    justify-content: space-between;
  }

  .head-fix li {
    padding: 0 10px;
    border-right: 1px solid #fff;
  }

  .head-fix a {
    color: #fff;
  }

  .right {
    color: #fff;
    display: flex;
  }

  .right span {
    cursor: pointer;
    margin-left: 10px;
  }

  .top1 {
    display: flex;
    justify-content: space-around;
  }

  .top2 {
    display: flex;
    justify-content: space-around;
  }
a.back{
  position: fixed;
  z-index: 55;
  width: 50px;
  height: 50px;
  bottom: 100px;
  right: 30px;
  background-color: #797979;
  display: block;
  cursor: pointer;
}
a.back:hover{
  background-color: #FC7878;
}
a.back i{
  width: 25px;
  height: 22px;
  float: left;
  margin-left: 15px;
  margin-top: 15px;
  background-image: url("../assets/img/suspension-bg.png");
  background-position: -28px -25px;
  background-repeat: no-repeat;
}
</style>



